import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import MainCanvas from '@/components/MainCanvas'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'MainCanvas',
      component: MainCanvas
    }
  ]
})



